.Board {
	position: relative;
	margin: 0 auto;
	margin-top: 20px;
	background-color: #000;
	background-image: linear-gradient(#333 1px, transparent 1px), linear-gradient(90deg, #333 1px, transparent 1px);
}

.Cell {
	background-color: #ccc;
	position: absolute;
}

.controls {
	margin: 20px auto;
}

.btn {
	padding: 5px 20px;
	margin-right: 10px;
}

.btn:last-child {
	margin-right: 0;
}
